import { AbstractTarget } from './target.abstract';
import { openUrlByPost } from '@engine/crud';

export class SelfTarget extends AbstractTarget {
    private byPost?: boolean;

    private parameter?: string;

    public constructor(target: string, url: string, op: { byPost?: boolean; parameter?: string }) {
        super(target, url, op);

        this.byPost = op.byPost;

        this.parameter = op.parameter;
    }

    public handle() {
        return this.byPost ? openUrlByPost(this.url, { __parameters__: this.parameter }, this.target) : window.open(this.url, this.target);
    }
}
